'use client';

import { useRouter, useSearchParams } from 'next/navigation';
import { motion } from 'framer-motion';
import { VERTICALS, VERTICAL_COLORS } from '@/lib/constants';
import type { Vertical } from '@/lib/types';

const verticalList: Vertical[] = ['ARM', 'Healthcare', 'BPO', 'DebtNext', 'LoanServicing'];

export function VerticalFilter() {
  const router = useRouter();
  const searchParams = useSearchParams();

  // Get currently selected verticals from URL
  const selectedVerticals = searchParams.get('verticals')?.split(',').filter(Boolean) as Vertical[] || [];

  const toggleVertical = (vertical: Vertical) => {
    const newParams = new URLSearchParams(searchParams.toString());

    if (selectedVerticals.includes(vertical)) {
      // Remove vertical
      const filtered = selectedVerticals.filter((v) => v !== vertical);
      if (filtered.length > 0) {
        newParams.set('verticals', filtered.join(','));
      } else {
        newParams.delete('verticals');
      }
    } else {
      // Add vertical
      newParams.set('verticals', [...selectedVerticals, vertical].join(','));
    }

    router.push(`?${newParams.toString()}`, { scroll: false });
  };

  return (
    <div className="flex flex-wrap gap-2">
      {verticalList.map((vertical) => {
        const config = VERTICALS[vertical];
        const isActive = selectedVerticals.includes(vertical);
        const color = VERTICAL_COLORS[vertical];

        return (
          <motion.button
            key={vertical}
            onClick={() => toggleVertical(vertical)}
            className={`filter-chip ${isActive ? 'active' : ''}`}
            style={{
              borderColor: isActive ? color : undefined,
              backgroundColor: isActive ? `${color}20` : undefined,
            }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            layout
          >
            <span
              className="w-2 h-2 rounded-full flex-shrink-0"
              style={{ backgroundColor: color }}
            />
            <span className={isActive ? 'text-white' : ''}>
              {config.abbreviation}
            </span>
          </motion.button>
        );
      })}
    </div>
  );
}
