'use server';

import { revalidatePath } from 'next/cache';
import { execSync } from 'child_process';
import path from 'path';
import { getPublishQueue } from '@/lib/file-system';
import type { ActionResponse, Vertical, Platform, PublishableItem } from '@/lib/types';

// Base path to parent directory
const BASE_PATH = path.join(process.cwd(), '..');

/**
 * Refresh the dashboard page data
 */
export async function refreshDashboard(): Promise<ActionResponse> {
  try {
    revalidatePath('/');
    return { success: true, message: 'Dashboard refreshed' };
  } catch (error) {
    return { success: false, error: String(error) };
  }
}

