/**
 * Trigger the autopilot runner
 * Executes the Python autopilot script with optional vertical filter
 */
export async function triggerAutopilot(
  verticals?: Vertical[],
  dryRun: boolean = false
): Promise<ActionResponse> {
  try {
    const scriptPath = path.join(BASE_PATH, 'scripts', 'autopilot_runner.py');
    let command = `python3 "${scriptPath}"`;

    // Add dry-run flag if specified
    if (dryRun) {
      command += ' --dry-run';
    }

    // Add vertical filter if specified
    if (verticals && verticals.length > 0) {
      command += ` --verticals ${verticals.join(',')}`;
    }

    console.log(`[Autopilot] Running: ${command}`);

    // Execute the autopilot script
    const output = execSync(command, {
      cwd: BASE_PATH,
      encoding: 'utf-8',
      timeout: 300000, // 5 minute timeout
      stdio: ['pipe', 'pipe', 'pipe'],
    });

    console.log(`[Autopilot] Output: ${output}`);

    // Revalidate all pages
    revalidatePath('/');
    revalidatePath('/intel');
    revalidatePath('/vault');

    return {
      success: true,
      message: `Autopilot completed for ${verticals?.join(', ') || 'all verticals'}`,
      data: output,
    };
  } catch (error) {
    console.error('[Autopilot] Error:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : String(error),
    };
  }
}

/**
 * Publish an asset to a platform
 * Executes the publish_content.py script
 */
export async function publishAsset(
  platform: Platform,
  filePath: string
): Promise<ActionResponse> {
  try {
    const scriptPath = path.join(BASE_PATH, 'scripts', 'publish_content.py');
    const command = `python3 "${scriptPath}" --platform ${platform} --file "${filePath}" --dry-run`;

    console.log(`[Publish] Running: ${command}`);

    // Execute the publish script
    const output = execSync(command, {
      cwd: BASE_PATH,
      encoding: 'utf-8',
      timeout: 120000, // 2 minute timeout
      stdio: ['pipe', 'pipe', 'pipe'],
    });

    console.log(`[Publish] Output: ${output}`);

    // Revalidate relevant pages
    revalidatePath('/');
    revalidatePath('/vault');

    return {
      success: true,
      message: `Published to ${platform}`,
      data: output,
    };
  } catch (error) {
    console.error('[Publish] Error:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : String(error),
    };
  }
}

/**
 * Get the publish queue as a server action
 */
export async function getPublishQueueAction(): Promise<ActionResponse<PublishableItem[]>> {
  try {
    const items = await getPublishQueue();
    return {
      success: true,
      data: items,
    };
  } catch (error) {
    console.error('[PublishQueue] Error:', error);
    return {
      success: false,
      error: error instanceof Error ? error.message : String(error),
    };
  }
}

