'use client';

import { useEffect, useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import {
  X,
  Upload,
  Linkedin,
  Mail,
  Globe,
  Loader2,
  CheckCircle2,
  AlertCircle,
} from 'lucide-react';
import { publishAsset, getPublishQueueAction } from '@/app/actions';
import { VERTICALS, VERTICAL_COLORS } from '@/lib/constants';
import type { PublishableItem, Platform } from '@/lib/types';

interface PublishPopoverProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

const platformIcons: Record<Platform, typeof Linkedin> = {
  linkedin: Linkedin,
  zoho: Mail,
  web: Globe,
};

const platformLabels: Record<Platform, string> = {
  linkedin: 'LinkedIn',
  zoho: 'Zoho Campaigns',
  web: 'Website',
};

export function PublishPopover({ open, onOpenChange }: PublishPopoverProps) {
  const [items, setItems] = useState<PublishableItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [publishing, setPublishing] = useState<string | null>(null);
  const [publishStatus, setPublishStatus] = useState<Record<string, 'success' | 'error'>>({});

  useEffect(() => {
    if (open) {
      loadQueue();
    }
  }, [open]);

  const loadQueue = async () => {
    setLoading(true);
    try {
      const result = await getPublishQueueAction();
      if (result.success && result.data) {
        setItems(result.data);
      }
    } catch {
      // Handle error
    }
    setLoading(false);
  };

  const handlePublish = async (item: PublishableItem) => {
    setPublishing(item.id);

    try {
      const result = await publishAsset(item.platform, item.path);
      setPublishStatus((prev) => ({
        ...prev,
        [item.id]: result.success ? 'success' : 'error',
      }));
    } catch {
      setPublishStatus((prev) => ({
        ...prev,
        [item.id]: 'error',
      }));
    }

    setPublishing(null);
  };

  return (
    <AnimatePresence>
      {open && (
        <>
          {/* Backdrop */}
          <motion.div
            className="fixed inset-0 bg-black/40 backdrop-blur-sm z-40"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => onOpenChange(false)}
          />

          {/* Popover */}
          <motion.div
            className="fixed left-[280px] bottom-24 w-96 z-50"
            initial={{ opacity: 0, y: 20, scale: 0.95 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 20, scale: 0.95 }}
          >
            <div className="glass-panel overflow-hidden">
              {/* Header */}
              <div className="flex items-center justify-between p-4 border-b border-white/10">
                <div className="flex items-center gap-3">
                  <Upload className="w-5 h-5 text-tsi-blue-light" />
                  <h3 className="font-semibold text-white">Publish Queue</h3>
                  {items.length > 0 && (
                    <span className="px-2 py-0.5 rounded-full bg-tsi-blue/20 text-tsi-blue-light text-xs font-medium">
                      {items.length}
                    </span>
                  )}
                </div>
                <button
                  onClick={() => onOpenChange(false)}
                  className="p-1.5 rounded-lg hover:bg-white/10 transition-colors"
                >
                  <X className="w-4 h-4 text-white/60" />
                </button>
              </div>

              {/* Content */}
              <div className="max-h-80 overflow-y-auto">
                {loading ? (
                  <div className="flex items-center justify-center py-12">
                    <Loader2 className="w-6 h-6 text-tsi-blue-light animate-spin" />
                  </div>
                ) : items.length === 0 ? (
                  <div className="text-center py-12 px-4">
                    <Upload className="w-10 h-10 text-white/20 mx-auto mb-3" />
                    <p className="text-white/60 text-sm">No items ready to publish</p>
                  </div>
                ) : (
                  <div className="divide-y divide-white/10">
                    {items.map((item) => {
                      const Icon = platformIcons[item.platform];
                      const color = VERTICAL_COLORS[item.vertical];
                      const status = publishStatus[item.id];
                      const isPublishing = publishing === item.id;

                      return (
                        <motion.div
                          key={item.id}
                          className="p-4 hover:bg-white/5 transition-colors"
                          initial={{ opacity: 0 }}
                          animate={{ opacity: 1 }}
                        >
                          <div className="flex items-start gap-3">
                            <div
                              className="w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0"
                              style={{ backgroundColor: `${color}20` }}
                            >
                              <Icon className="w-4 h-4" style={{ color }} />
                            </div>
                            <div className="flex-1 min-w-0">
                              <p className="text-sm font-medium text-white truncate">
                                {item.title}
                              </p>
                              <div className="flex items-center gap-2 mt-1">
                                <span
                                  className="w-1.5 h-1.5 rounded-full"
                                  style={{ backgroundColor: color }}
                                />
                                <span className="text-xs text-white/60">
                                  {VERTICALS[item.vertical].abbreviation}
                                </span>
                                <span className="text-white/30">Â·</span>
                                <span className="text-xs text-white/60">
                                  {platformLabels[item.platform]}
                                </span>
                              </div>
                            </div>
                            <div className="flex-shrink-0">
                              {status === 'success' ? (
                                <CheckCircle2 className="w-5 h-5 text-green-400" />
                              ) : status === 'error' ? (
                                <AlertCircle className="w-5 h-5 text-red-400" />
                              ) : isPublishing ? (
                                <Loader2 className="w-5 h-5 text-tsi-blue-light animate-spin" />
                              ) : (
                                <button
                                  onClick={() => handlePublish(item)}
                                  className="px-3 py-1.5 rounded-lg bg-tsi-blue/20 text-tsi-blue-light text-xs font-medium hover:bg-tsi-blue/30 transition-colors"
                                >
                                  Publish
                                </button>
                              )}
                            </div>
                          </div>
                        </motion.div>
                      );
                    })}
                  </div>
                )}
              </div>

              {/* Footer */}
              {items.length > 0 && (
                <div className="p-4 border-t border-white/10">
                  <button
                    className="w-full btn-primary text-sm"
                    onClick={() => {
                      // Publish all items
                      items.forEach((item) => {
                        if (!publishStatus[item.id]) {
                          handlePublish(item);
                        }
                      });
                    }}
                  >
                    Publish All ({items.length})
                  </button>
                </div>
              )}
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
}
