'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, Zap, Loader2, CheckCircle2, AlertCircle } from 'lucide-react';
import { triggerAutopilot } from '@/app/actions';
import { VERTICALS, VERTICAL_COLORS } from '@/lib/constants';
import type { Vertical } from '@/lib/types';

interface AutopilotModalProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

const verticals: Vertical[] = ['ARM', 'Healthcare', 'BPO', 'DebtNext', 'LoanServicing'];

export function AutopilotModal({ open, onOpenChange }: AutopilotModalProps) {
  const [selectedVerticals, setSelectedVerticals] = useState<Vertical[]>([]);
  const [status, setStatus] = useState<'idle' | 'running' | 'success' | 'error'>('idle');
  const [message, setMessage] = useState('');

  const toggleVertical = (vertical: Vertical) => {
    setSelectedVerticals((prev) =>
      prev.includes(vertical)
        ? prev.filter((v) => v !== vertical)
        : [...prev, vertical]
    );
  };

  const handleRun = async () => {
    setStatus('running');
    setMessage('');

    try {
      const result = await triggerAutopilot(
        selectedVerticals.length > 0 ? selectedVerticals : undefined
      );

      if (result.success) {
        setStatus('success');
        setMessage(result.message || 'Autopilot completed successfully');
      } else {
        setStatus('error');
        setMessage(result.error || 'An error occurred');
      }
    } catch {
      setStatus('error');
      setMessage('Failed to run autopilot');
    }
  };

  const handleClose = () => {
    if (status !== 'running') {
      onOpenChange(false);
      // Reset state after close animation
      setTimeout(() => {
        setStatus('idle');
        setMessage('');
        setSelectedVerticals([]);
      }, 200);
    }
  };

  return (
    <AnimatePresence>
      {open && (
        <>
          {/* Backdrop */}
          <motion.div
            className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={handleClose}
          />

          {/* Modal */}
          <motion.div
            className="fixed left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-md z-50"
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.95 }}
          >
            <div className="glass-panel p-6 mx-4">
              {/* Header */}
              <div className="flex items-center justify-between mb-6">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 rounded-xl bg-tsi-blue/20 flex items-center justify-center">
                    <Zap className="w-5 h-5 text-tsi-blue-light" />
                  </div>
                  <div>
                    <h2 className="text-lg font-semibold text-white">Run Autopilot</h2>
                    <p className="text-sm text-white/60">Execute daily content run</p>
                  </div>
                </div>
                <button
                  onClick={handleClose}
                  className="p-2 rounded-lg hover:bg-white/10 transition-colors"
                  disabled={status === 'running'}
                >
                  <X className="w-5 h-5 text-white/60" />
                </button>
              </div>

              {/* Content */}
              {status === 'idle' && (
                <>
                  <p className="text-sm text-white/70 mb-4">
                    Select verticals to run, or leave empty to run all:
                  </p>

                  <div className="flex flex-wrap gap-2 mb-6">
                    {verticals.map((vertical) => {
                      const config = VERTICALS[vertical];
                      const isActive = selectedVerticals.includes(vertical);
                      const color = VERTICAL_COLORS[vertical];

                      return (
                        <motion.button
                          key={vertical}
                          onClick={() => toggleVertical(vertical)}
                          className={`filter-chip ${isActive ? 'active' : ''}`}
                          style={{
                            borderColor: isActive ? color : undefined,
                            backgroundColor: isActive ? `${color}20` : undefined,
                          }}
                          whileHover={{ scale: 1.05 }}
                          whileTap={{ scale: 0.95 }}
                        >
                          <span
                            className="w-2 h-2 rounded-full"
                            style={{ backgroundColor: color }}
                          />
                          <span>{config.abbreviation}</span>
                        </motion.button>
                      );
                    })}
                  </div>

                  <div className="flex gap-3">
                    <button
                      onClick={handleClose}
                      className="btn-secondary flex-1"
                    >
                      Cancel
                    </button>
                    <button
                      onClick={handleRun}
                      className="btn-primary flex-1"
                    >
                      <Zap className="w-4 h-4" />
                      Run Now
                    </button>
                  </div>
                </>
              )}

              {status === 'running' && (
                <div className="text-center py-8">
                  <Loader2 className="w-12 h-12 text-tsi-blue-light animate-spin mx-auto mb-4" />
                  <p className="text-white font-medium">Running Autopilot...</p>
                  <p className="text-sm text-white/60 mt-1">
                    {selectedVerticals.length > 0
                      ? `Processing ${selectedVerticals.join(', ')}`
                      : 'Processing all verticals'}
                  </p>
                </div>
              )}

              {status === 'success' && (
                <div className="text-center py-8">
                  <div className="w-16 h-16 rounded-full bg-green-500/20 flex items-center justify-center mx-auto mb-4">
                    <CheckCircle2 className="w-8 h-8 text-green-400" />
                  </div>
                  <p className="text-white font-medium">Autopilot Complete</p>
                  <p className="text-sm text-white/60 mt-1">{message}</p>
                  <button onClick={handleClose} className="btn-primary mt-6">
                    Done
                  </button>
                </div>
              )}

              {status === 'error' && (
                <div className="text-center py-8">
                  <div className="w-16 h-16 rounded-full bg-red-500/20 flex items-center justify-center mx-auto mb-4">
                    <AlertCircle className="w-8 h-8 text-red-400" />
                  </div>
                  <p className="text-white font-medium">Error Occurred</p>
                  <p className="text-sm text-white/60 mt-1">{message}</p>
                  <div className="flex gap-3 mt-6">
                    <button onClick={handleClose} className="btn-secondary flex-1">
                      Close
                    </button>
                    <button onClick={() => setStatus('idle')} className="btn-primary flex-1">
                      Try Again
                    </button>
                  </div>
                </div>
              )}
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
}
