'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { motion } from 'framer-motion';
import {
  Activity,
  Radar,
  Library,
  Zap,
  Upload,
  CheckCircle2,
} from 'lucide-react';
import { VerticalFilter } from './VerticalFilter';
import { AutopilotModal } from './AutopilotModal';
import { PublishPopover } from './PublishPopover';
import { useState } from 'react';

const NAV_ITEMS = [
  {
    label: 'Pulse',
    href: '/',
    icon: Activity,
    description: 'System overview',
  },
  {
    label: 'Intel Room',
    href: '/intel',
    icon: Radar,
    description: 'Market intelligence',
  },
  {
    label: 'Vault',
    href: '/vault',
    icon: Library,
    description: 'Asset library',
  },
];

export function Sidebar() {
  const pathname = usePathname();
  const [autopilotOpen, setAutopilotOpen] = useState(false);
  const [publishOpen, setPublishOpen] = useState(false);

  return (
    <>
      <aside className="fixed left-0 top-0 h-screen w-[260px] glass-panel rounded-none border-r border-white/10 z-50 flex flex-col">
        {/* Logo */}
        <div className="p-6 border-b border-white/10">
          <Link href="/" className="flex items-center gap-3 group">
            <div className="w-10 h-10 rounded-xl bg-tsi-blue/20 flex items-center justify-center group-hover:bg-tsi-blue/30 transition-colors">
              <span className="text-tsi-blue-light font-bold text-lg">TSI</span>
            </div>
            <div>
              <h1 className="font-semibold text-white group-hover:text-tsi-blue-light transition-colors">
                Mission Control
              </h1>
              <p className="text-xs text-white/50">Marketing Autopilot</p>
            </div>
          </Link>
        </div>

        {/* Navigation */}
        <nav className="p-4 space-y-1">
          {NAV_ITEMS.map((item) => {
            const isActive = pathname === item.href;
            const Icon = item.icon;

            return (
              <Link key={item.href} href={item.href}>
                <motion.div
                  className={`nav-link ${isActive ? 'active' : ''}`}
                  whileHover={{ x: 4 }}
                  whileTap={{ scale: 0.98 }}
                >
                  <Icon className="w-5 h-5" />
                  <div className="flex-1">
                    <span className="font-medium">{item.label}</span>
                  </div>
                  {isActive && (
                    <motion.div
                      layoutId="nav-indicator"
                      className="w-1.5 h-1.5 rounded-full bg-tsi-blue-light"
                    />
                  )}
                </motion.div>
              </Link>
            );
          })}
        </nav>

        {/* Vertical Filter */}
        <div className="px-4 py-3 border-t border-white/10">
          <p className="text-xs font-medium text-white/50 uppercase tracking-wider mb-3 px-2">
            Filter by Vertical
          </p>
          <VerticalFilter />
        </div>

        {/* Quick Actions */}
        <div className="px-4 py-3 border-t border-white/10">
          <p className="text-xs font-medium text-white/50 uppercase tracking-wider mb-3 px-2">
            Quick Actions
          </p>
          <div className="space-y-2">
            <motion.button
              onClick={() => setAutopilotOpen(true)}
              className="w-full flex items-center gap-3 px-4 py-2.5 rounded-xl bg-tsi-blue/20 text-tsi-blue-light hover:bg-tsi-blue/30 transition-colors"
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
            >
              <Zap className="w-4 h-4" />
              <span className="font-medium text-sm">Run Autopilot</span>
            </motion.button>

            <motion.button
              onClick={() => setPublishOpen(!publishOpen)}
              className="w-full flex items-center gap-3 px-4 py-2.5 rounded-xl bg-white/5 text-white/70 hover:bg-white/10 hover:text-white transition-colors relative"
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
            >
              <Upload className="w-4 h-4" />
              <span className="font-medium text-sm">Publish Queue</span>
            </motion.button>
          </div>
        </div>

        {/* System Status */}
        <div className="mt-auto p-4 border-t border-white/10">
          <div className="flex items-center gap-3 px-2">
            <div className="relative">
              <div className="w-3 h-3 rounded-full bg-green-500" />
              <div className="absolute inset-0 w-3 h-3 rounded-full bg-green-500 animate-ping opacity-75" />
            </div>
            <div className="flex-1">
              <p className="text-sm font-medium text-white">System Online</p>
              <p className="text-xs text-white/50">All agents operational</p>
            </div>
            <CheckCircle2 className="w-4 h-4 text-green-500" />
          </div>
        </div>
      </aside>

      {/* Modals */}
      <AutopilotModal open={autopilotOpen} onOpenChange={setAutopilotOpen} />
      <PublishPopover open={publishOpen} onOpenChange={setPublishOpen} />
    </>
  );
}
